.banner-text{
    background-color: #d60909;
    opacity: .7;
    width: 95%;
    margin: auto;
    border-radius: 25px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 5%;
}

.banner-text h1{
    font-size: 50px;
    -webkit-text-stroke: 1.5px black;
    font-weight: bold;
    color: white;
    text-align: center;
    padding-top: 1%;
}

.header-text{
    font: Arial;
    font-size: 20px;
    color: white;
    padding-right: 15px;
}

.header-text:hover{
    font: Arial;
    font-size: 20px;
    color: pink;
}

.profile-links{
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: auto;
}

.profile-links i{
    color: white;
    font-size: 7em;
}